/* ==========================================================================
   ReactResource
   ========================================================================== */

export { defaults } from './utils/request';
import isObject from 'lodash/isObject';
import each from 'lodash/each';

export default function ReactResource(url, mappings = {}, customActions = {}) {
  function ResourceModel(data = {}) {
    if (isObject(data)) each(data, (val, key) => this[key] = val);

    return this;
  };

  // Build actions
  const actionsBuilder = new ActionsBuilder(url, mappings, customActions);
  actionsBuilder.classMethods(ResourceModel);
  actionsBuilder.instanceMethods(ResourceModel);

  return ResourceModel;
}

if (typeof window !== undefined) {
  window.ReactResource = ReactResource;
}



